.Moderator_Dashbord_Container {
    // height: 100%;
    height: 100vh;
    width: 100vw;
    background-color: #222528;
    color: #ffffff;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .Moderator_Dashbord_Content {
        flex: 1;
        display: flex;
        flex-direction: row;

        .Moderator_Dashbord_Header {
            flex: 25%;
        }

        .Moderator_Dashbord_Body {
            flex: 75%;
        }
    }
}

@media only screen and (max-width: 600px) {
    .Moderator_Dashbord_Container {
        // 		flex-direction: column;
        // 		justify-content: center;
        // 		align-items: center;

        .Moderator_Dashbord_Content {
            display: block;

            .Moderator_Dashbord_Header {
                height: 100vh;
                position: absolute;
                z-index: 100;
                flex: 0%;
                // 			// background-color: rebeccapurple;
            }

            .Moderator_Dashbord_Body {
                flex: 100%;
                // 			// background-color: red;
            }
        }
    }
}
