.BookUploadFadeOut {
    opacity: 0;
    visibility: hidden;
    width: 0;
    height: 0;
    transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
}

.BookUploadFadeIn {
    opacity: 1;
    visibility: visible;
    width: 100%;
    height: 500px;
    overflow-y: auto;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
    transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
}
.BookUploadModContainer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);

    .popupCard {
        //outside of the container
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
        width: 60%;
        height: 50%;
        background-color: white;
        color: #000000;
        border-radius: 10px;

        .inputField {
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 1px solid #ccc;
            border-radius: 10px;
            background-color: #eee;
            color: #000;
            font-size: 20px;
            padding: 10px;
            cursor: pointer;

            input {
                width: 100%;
                height: 100%;
                border: none;
                background-color: #eee;
                color: #000;
                font-size: 15px;
                padding: 10px;
                cursor: pointer;
            }
        }

        .close {
            content: "x";
            cursor: pointer;
            position: fixed;
            left: calc(100% - 20px);
            top: calc(0vh - 10px);
            background: #000000;
            width: 25px;
            height: 25px;
            border-radius: 50%;
            line-height: 20px;
            text-align: center;
            border: 1px solid #999;
            font-size: 20px;
            color: white;
        }

        .popupCardHeader {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 80px;
            // border-bottom: 1px solid #999;
            border-radius: 10px 10px 0px 0px;
            background-color: #008080;
            color: white;
        }
        .popupCardBody {
            display: flex;

            align-items: flex-start;
            width: 100%;
            height: 75%;
            padding: 5px;

            .ThumbnailText {
                font-size: 20px;
                font-weight: bold;
                color: #008080;
            }

            .left {
                flex: 1;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                gap: 10px;

                .previewImg {
                    height: 200px;
                    max-width: 90%;
                    //fit the image to the container
                    object-fit: scale-down;
                    background-color: #ccc;
                    border: #008080 solid 5px;
                }
                .inputField {
                    height: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border: 1px solid #ccc;
                    border-radius: 10px;
                    background-color: #eee;
                    color: #000;
                    font-size: 20px;
                    padding: 10px;
                    cursor: pointer;

                    input {
                        width: 100%;
                        height: 100%;
                        border: none;
                        background-color: #eee;
                        color: #000;
                        font-size: 15px;
                        padding: 10px;
                        cursor: pointer;
                    }
                }
            }
            .right {
                flex: 1;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                gap: 10px;

                .UplodedFileInfo {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: flex-start;
                    text-overflow: ellipsis;
                    height: 210px;
                    gap: 15px;
                    div {
                        white-space: nowrap;
                        width: 300px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        text-align: start;
                    }
                }
            }
        }
        .popupCardFooter {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 50px;
            background-color: #008080;
            border-radius: 0px 0px 10px 10px;
            color: white;
            cursor: pointer;

            &:hover {
                background-color: #1db1b1;
            }
        }
    }
}

.errorText {
    margin: 8px;
    color: red;
    font-size: 15px;
    font-weight: bold;
}
