.teachers_profile_container {
	flex: none;
	height: 100vh;
	overflow: hidden;
	@media screen and (max-width: 760px) {
		overflow: auto;
	}
	// background-color: aqua;

	.teachers_profile_header_container {
		width: 100vw;
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;

		box-shadow: 0px 0px 10px #00000029;
	}
	.teachers_profile_body_container {
		display: flex;
		padding-top: 20px;

		@media screen and (max-width: 760px) {
			flex-direction: column;
			justify-content: center;
			align-items: center;
		}

		.teachers_profile_body_left_container {
			// background-color: aqua;
			flex: 1;
			display: flex;
			flex-direction: column;
			align-items: flex-end;
			min-width: 350px;
			padding-right: 20px;

			.teachers_profile_body_left_image {
				width: 300px;
				height: 300px;
				border-radius: 50%;
				border: 1px solid #92c2d9;
				margin-bottom: 20px;
			}
			.teachers_profile_card {
				width: 300px;
				display: flex;
				flex-direction: column;
				align-items: flex-start;

				//components
				.icon {
					margin-right: 10px;
				}
				.divider {
					margin: 5px 0;
					height: 2px;
					width: 100%;
					background-color: #92c2d9;
				}

				.teachers_name {
					width: 100%;
					text-align: start;
					font-size: 20px;
					font-weight: bold;
					margin-bottom: 10px;
				}

				.teachers_email {
					width: 100%;
					text-align: start;
					font-size: 16px;
					font-weight: bold;
					margin-bottom: 10px;
				}

				.Edit_profile_btn {
					width: 100%;
					height: 40px;
					background-color: #00bcd4;
					border-radius: 5px;
					color: white;
					font-size: 15px;
					font-weight: bold;
					text-align: center;
					line-height: 40px;
					cursor: pointer;
				}

				.teachers_profile_card_info {
					display: flex;
					flex-direction: row;
					margin: 10px 0px;
					// background-color: #00bcd4;
				}

				.teachers_profile_card_edit_info {
					display: flex;
					flex-direction: column;
					margin: 10px 0px;
					// background-color: #00bcd4;

					div {
						display: flex;
						flex-direction: row;
						align-items: center;
						padding: 3px 0px;

						div {
							white-space: nowrap;
							// width: 200px;
							overflow: hidden;
							text-overflow: ellipsis;
						}
					}

					.editBtnContainer {
						display: flex;
						width: 100%;
						flex-direction: row;
						justify-content: space-between;
						align-items: center;
						margin-top: 10px;

						.btn {
							padding: 10px;
							width: 75px;
							display: flex;
							justify-content: center;
							align-items: center;
							background-color: #9cd1d9;
							border-radius: 5px;
							color: #21262d;
							font-weight: bold;
							cursor: pointer;
							margin: 0px 20px;

							&:hover {
								background-color: #00bcd4;
								color: white;
							}
						}

						@media screen and (max-width: 768px) {
							flex-direction: column;
							align-items: flex-start;
							.btn {
								margin: 0px;
							}
						}
					}
				}
				.teachers_profile_card_rating {
					display: flex;
					flex-direction: row;
					align-items: center;
					padding: 3px 0px;
					margin: 3px 0px;
					span {
						margin-left: 10px;
					}
				}
				.teachers_profile_card_orgs {
					display: flex;
					flex-direction: row;
					align-items: center;
					padding: 3px 0px;
					span {
						margin-left: 10px;
					}
				}
			}

			@media screen and (max-width: 768px) {
				flex-direction: column;
				align-items: center;
				.teachers_profile_body_left_image {
					width: 200px;
					height: 200px;
					margin-bottom: 20px;
				}
			}
		}
		.teachers_profile_body_right_container {
			flex: 2.5;
			height: calc(100vh - 100px);
			overflow-y: scroll;
			// background-color: #00bcd4;

			.about_teacher {
				margin: 20px 20px;
				padding: 20px;
				display: flex;
				flex-direction: column;
				justify-content: flex-start;
				align-items: flex-start;

				.about_teacher_header {
					font-size: 20px;
					font-weight: bold;
					margin-bottom: 10px;
				}

				.about_teacher_body {
					font-size: 16px;
					margin-bottom: 10px;
					text-align: justify;
				}
			}
			.courses {
				margin: 20px 20px;
				padding: 20px;
				display: flex;
				flex-direction: column;
				justify-content: flex-start;
				align-items: flex-start;

				.courses_header {
					font-size: 20px;
					font-weight: bold;
					margin-bottom: 10px;
				}
				.courses_body {
					font-size: 16px;
					margin-bottom: 10px;
				}
			}
		}
	}
}
