.main {
    width: 80vw;
}

.path {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 20px;
    background-color: #f9d671;
    padding: 5px;
    cursor: pointer;
    width: 200px;

    .Icon {
        font-size: 30px;
    }
}

.course_name {
    font-size: 30px;
    font-weight: bold;
    text-align: left;
    margin-top: 30px;
    margin-bottom: 30px;
}
.mid_row_top {
    display: flex;
    flex-direction: row;
    gap: 10px;

    @media screen and (max-width: 768px) {
        flex-direction: column;
    }
}
.mid_row {
    margin-top: 20px;
    // background-color: #5bea72;

    .mid_row_left {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 10px;
        // background-color: #5731b1;
        box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;

        .mid_row_left_top {
            display: flex;
            width: 100%;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            // background-color: #5f5c51;

            @media (max-width: 768px) {
                flex-direction: column;
            }

            .user_information {
                display: flex;
                flex-direction: row;
                padding: 20px;
                // background-color: #5bea72;

                @media (max-width: 768px) {
                    flex-direction: column;
                }

                .user_pic {
                    flex: 1;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    width: 100%;
                    height: 100%;
                    margin-right: 10px;
                    // background-color: #5bea72;
                    border-radius: 50%;

                    .user_pic_img {
                        width: 80px;
                        height: 80px;
                        border-radius: 50%;
                        // background-color: #5bea72;
                        box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
                    }
                }

                .name_info {
                    display: flex;
                    flex-direction: column;
                    margin: 0px 10px;
                    justify-content: center;
                    align-items: center;
                    // background-color: #656e67;

                    @media (max-width: 768px) {
                        align-items: center;
                    }

                    .name_text {
                        font-size: 20px;
                        font-weight: bold;
                        color: #000000;
                        margin-top: 10px;
                        // background-color: #5bea72;
                    }

                    .info_text {
                        font-size: 15px;
                        color: #000000;
                        margin-top: 10px;
                        // background-color: #5bea72;
                    }
                }
            }

            .start_class {
                padding: 10px 20px;
                margin-right: 2rem;
                border-radius: 10px;
                font-size: 1.5rem;
                font-weight: bold;
                background-color: #f9d671;

                &:hover {
                    background-color: #facc43;
                }

                .start_class_text {
                    text-decoration: none;
                }

                @media (max-width: 768px) {
                    margin-right: 0px;
                    margin-top: 10px;
                }
            }
        }

        .course_details {
            display: flex;
            flex-direction: row;
            width: 100%;
            justify-content: space-between;
            margin-top: 10px;
            margin-bottom: 10px;

            .course_details_tiles_left {
                font-size: 30px;
                font-weight: bold;
                padding: 10px;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                padding: 0px 10px;
            }

            .course_details_text {
                font-size: 15px;
                font-weight: lighter;
            }
            .course_details_tiles_right {
                display: flex;
                width: 100px;
                flex-direction: column;
                align-items: center;
                padding: 0px 10px;
            }

            .course_details_tile {
                display: flex;
                flex-direction: column;
                padding: 10px;
                border-radius: 15px;
                cursor: pointer;

                .course_details_tile_title {
                    font-size: 20px;
                    font-weight: bold;
                    color: #000000;
                    font-size: 1.3rem;
                    // background-color: #5bea72;
                }

                .num_chp {
                    font-size: 1.2rem;
                    font-weight: bold;
                    // background-color: #5bea72;
                }
            }

            .course_details_tile:hover {
                // box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
                background-color: aliceblue;
            }
        }
    }

    .mid_row_right {
        min-width: 300px;
        display: flex;
        flex-direction: column;
        border-radius: 10px;
        padding: 15px;
        font-size: large;
        // background-color: #29a9b3;
        box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;

        .date_table {
            thead {
                background-color: #f9d671;
            }
        }
        td {
            padding: 5px;
        }
        tr:nth-child(even) {
            background-color: #c4c4c4;
        }
        .tg-0lax {
            text-align: start;
            font-size: 1.2rem;
        }
    }

    .rating_text {
        height: 50px;
        width: 50px;
        font-size: 35px;
        border: rgb(14, 199, 45) dashed 3px;
        border-radius: 50%;
        text-align: center;
    }
    // }
}

.chapter {
    font-size: 30px;
    font-weight: bold;
    text-align: left;
    margin-top: 60px;
}

.bottom_section {
    flex: 1;
    display: flex;
    flex-direction: row;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .chapter_info {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        // background-color: #5bea72;
        border-radius: 30px;
        // box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;

        .chapter_list_container {
            margin-top: 10px;
            display: flex;
            flex: 1;
            flex-direction: column;
            align-items: flex-start;
            width: 100%;
            justify-content: center;
            // background-color: #f9d671;

            .chapter_card {
                display: flex;
                flex: 1;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
                width: 100%;
                padding: 0.7rem;
                margin-bottom: 10px;
                border-radius: 10px;

                .headings {
                    display: flex;
                    flex: 1;
                    justify-content: center;
                    align-items: center;
                    height: 60px;
                    font-size: 15px;
                    border-radius: 10px;
                    font-weight: bold;
                    color: white;
                    // background-color: #1a5cce;

                    .icon_img {
                        width: 50px;
                        height: 50px;
                        border-radius: 50%;
                    }

                    .chpater_name_text {
                        text-decoration: none;
                        color: #1a5cce;
                    }

                    .chpater_title_text {
                        text-decoration: none;
                        color: #1a5cce;
                    }
                }

                .open_chapter {
                    display: flex;
                    flex: 1;
                    justify-content: center;
                    align-items: center;
                    height: 60px;
                    border-radius: 10px;
                    // background-color: #1a5cce;

                    .open_chapter_text {
                        text-decoration: none;
                        font-size: 1rem;
                        font-weight: bold;
                        color: #000000;
                        border-radius: 10px;
                        padding: 7px 20px;
                        background-color: #5bea72;
                    }
                }
            }
        }
    }

    .student_info {
        flex: 0.2;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 10px;
        margin-right: 10px;
        background-color: #414c5a;
        border-radius: 30px;
        box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;

        .student_info_title {
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            width: 100%;
            margin-bottom: 10px;
            // background-color: #f9d671;

            .student_info_title_text {
                font-size: 20px;
                font-weight: bold;
                font-style: italic;
                color: #ffffff;
            }
        }
    }
}
