.QuizFadeOut {
	opacity: 0;
	visibility: hidden;
	width: 0;
	height: 0;
	transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
}

.QuizFadeIn {
	opacity: 1;
	visibility: visible;
	width: 100%;
	height: 500px;
	overflow-y: auto;
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
	transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
}
.QuizFadeIn::-webkit-scrollbar {
	display: none;
}

