.routineRows {
	display: flex;
	width: 100%;
	height: 50px;
}

.routineColsIndex {
	font-size: 15px;
	position: relative;
	display: flex;
	flex: 1;
	align-items: center;
	justify-content: center;
	border: 1px solid #00bcd4;
}
.routineCols {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	border: 1px solid #00bcd4;
	font-size: 15px;

	//hover
	&:hover {
		background-color: #00bcd4;
		color: #ffffff;
	}
}
