.devReportFormPopupContainer {
    padding: 1rem;
    border-radius: 5px;
    width: 40%;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 280px;

    .label {
        width: 100%;
        font-size: 1.5rem;
        font-weight: bold;
        margin-bottom: 0.5rem;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        margin-left: 20px;
        color: #46178f;
    }
    .topic_input_holder {
        background-color: #ffffff;
        color: #46178f;
        width: 90%;
        padding: 15px;
        margin: 5px 0px;
        border-radius: 5px;
        font-size: 16px;
        border: 0.5px solid #46178f;
        outline: none;
        min-width: 250px;
    }
    .details_input_holder {
        width: 90%;
        background-color: #ffffff;
        min-width: 250px;
        color: #46178f;
        padding: 15px;
        margin: 5px 0px;
        border-radius: 5px;
        font-size: 16px;
        border: 0.5px solid #46178f;
        outline: none;
        resize: vertical;
    }
    .btn_container {
        width: 100%;
        display: flex;
        justify-content: space-evenly;
        .submit_button {
            background-color: #46178f;
            color: white;
            border: none;
            padding: 10px 20px;
            border-radius: 5px;
            font-size: 1.2rem;
            font-weight: bold;
            margin: 5px 10px;
            cursor: pointer;
            text-decoration: none;
            transition: all 0.2s ease-in-out;
        }
    }
}
