










.ModeratorPageContainer {
	.ModeratorPageHeader {
		.ModeratorPageHeaderFilterContainer {
			display: flex;
			flex-direction: row;
			justify-content: space-around;
			align-items: center;
			padding: 10px;
			margin-bottom: 5px;

	
	

			.ModeratorPageHeaderFilterButton {
				border: none;
				background-color: #ffffff;
				color: #000000;
				padding: 10px;
				font-size: 1.2em;
				outline: none;
			}
		}






	}
	

	.ModeratorPageBody {
		.ModeratorPageBodyTable {
			height: 80vh;
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: center;
			padding: 10px;
			border-bottom: 5px solid #222528;

			&:hover {
				border-bottom: 5px solid #282b2e;
			}

			.ModeratorPageBodyTableRow {
				overflow-y: scroll;
			}






			.ModeratorPageBodyTableRow::-webkit-scrollbar {
				display: none;
			}
		}
	}
}

