.mod_header_container {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;

	.mod_navbar_header {
		display: flex;
		flex-direction: row;
		justify-content: center;
		margin: 0 20px;
		padding: 10px 0;
		align-items: center;

		.logo {
			height: 60px;
			width: 60px;
		}

		.title {
			font-size: 20px;
			color: #ffffff;
		}
	}

	.hamburger_menu {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		width: 40px;
		height: 100%;
		margin-right: 15px;
		background-color: #1c1c1d;
		border-radius: 5px;
		transition: all 0.3s ease-in-out;
		cursor: pointer;

		.hamburger_menu_icon {
			width: 30px;
			height: 30px;
			transition: all 0.3s ease-in-out;
			margin: 0 10px;
			// background-color: red;

			.hamburger_menu_icon_line {
				width: 100%;
				height: 3px;
				margin: 5px 0;
				border-radius: 5px;
				background-color: #ffffff;
				transition: all 0.3s ease-in-out;
			}
		}
	}
}
