.ModeratorCourseListComponent_Container_Buttons {
    // width: 70px;
    // height: 40px;
    background-color: #5abb7a;
    border-radius: 5px;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 5px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    &:hover {
        background-color: #2d7444;
    }
}

@media only screen and (max-width: 850px) {
    .ModeratorCourseListComponent_Container_Buttons {
        font-size: 0.8rem;
    }
}
