.mod_navbar_menu_btn {
	width: 200px;
	border-radius: 5px;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	font-size: 1.2rem;
	margin: 20px 0px;
	padding: 10px 20px;
	// background-color: aquamarine;

	&:hover {
		background-color: #ffffff;
		color: #282b2e;
		cursor: pointer;
	}
}

.mod_navbar_menu_btn_selected {
	width: 200px;
	border-radius: 5px;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	font-size: 1.2rem;
	margin: 20px 0px;
	padding: 10px 20px;
	cursor: pointer;
	background-color: #5abb7a;
}
