.material_container {
	display: flex;
	flex-direction: row;
	font-size: 15px;
	width: 100%;
	// height: 100%;
	margin-bottom: 10px;
	border-radius: 10px;
	background-color: #f5f5f5;

	&:hover {
		// border: 1px solid #00a0e9;
		box-shadow: 0px 0px 10px 0px #424242;
	}

	.material_container_thumbnail {
		flex: 0.5;
		padding: 5px;
		justify-content: center;
		align-items: center;

		.material_container_thumbnail_img {
			width: 100px;
			object-fit: cover;
		}
	}
	.material_container_middle {
		flex: 1;
		padding: 5px;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	.material_container_middle_right {
		flex: 0.3;
		padding: 5px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		font-size: small;

		div {
			text-align: center;
		}
	}
	.material_container_right {
		flex: 0.4;
		padding: 5px;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
		font-size: 12px;
		cursor: pointer;

		.iconHolder {
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			color: white;
			font-size: 15px;
			min-width: 80px;
		}

		.icon {
			font-size: 1.3rem;
			margin-left: 10px;
			color: #fff;
		}
	}

	@media screen and (max-width: 768px) {
		flex-direction: column;
		.material_container_thumbnail {
			padding: 5px;
		}
		.material_container_middle {
			padding: 5px;
		}
		.material_container_download {
			padding: 5px;
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 12px;
		}
	}
}
