.SelectRoutineFadeOut {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    height: 0px;
    width: 0px;
    transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
}

.SelectRoutineFadeIn {
    min-width: 400px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: #ffffff;
    opacity: 1;
    visibility: visible;
    overflow-y: auto;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
    transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
}

.react-time-picker__clock {
    display: none;
}
.SelectRoutine {
    .RoutineMakerContainer {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 45px;
        border-radius: 8px;

        .selectedDay {
            background-color: #e05353;
        }

        .btn-primary {
            background-color: #e05353;
            height: 30px;
            margin-left: 10px;
            outline: none;
            color: #ffffff;
            border-radius: 5px;
            border-color: #cc4242;

            &:hover {
                background-color: #cc4242;
                border-color: #cc4242;
            }
        }

        .addButton {
            width: 100px;
            height: 30px;
            font-size: 1rem;
            margin: 0px 0px;
            padding: 0px 0px;
            background-color: white;
            color: #00bcd4;
            border-radius: 5px;
            cursor: pointer;
            margin-left: 20px;
        }

        .timePicker {
            background-color: white;
        }
        .routineRows {
            display: flex;
            width: 100%;
            height: 50px;
        }

        .routineColsIndex {
            font-size: 15px;
            position: relative;
            display: flex;
            flex: 1;
            align-items: center;
            justify-content: center;
            border: 1px solid #00bcd4;

            .routineColsIndex_Delete {
                position: absolute;
                top: 0;
                left: 0;
                height: 20px;
                width: 20px;
                color: #ffffff;
                background-color: #e05353;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                opacity: 0.5;
                transition: opacity 0.3s ease-in-out;

                &:hover {
                    opacity: 1;
                }

                &:active {
                    opacity: 0.5;
                }
            }
        }
        .routineCols {
            flex: 1;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            border: 1px solid #00bcd4;
            font-size: 15px;

            //hover
            &:hover {
                background-color: #00bcd4;
                color: #ffffff;
            }
        }

        .Options {
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            background-color: #00bcd4;

            .Options_left {
                display: flex;
                flex-direction: row;
                align-items: center;
            }

            .Options_right {
                padding: 15px;

                button {
                    width: 100px;
                    height: 30px;
                    font-size: 1rem;
                    margin: 0px 0px;
                    padding: 0px 0px;
                    background-color: white;
                    color: #00bcd4;
                    border-radius: 5px;
                    outline: none;
                    border: none;
                    cursor: pointer;
                    margin-left: 20px;
                    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
                        rgba(0, 0, 0, 0.12) 0px -12px 30px,
                        rgba(0, 0, 0, 0.12) 0px 4px 6px,
                        rgba(0, 0, 0, 0.17) 0px 12px 13px,
                        rgba(0, 0, 0, 0.09) 0px -3px 5px;
                }
            }

            .timeInput {
                height: 40px;
                outline: none;
                border: none;
                border-radius: 5px;
            }
        }

        .RoutineElementContainer {
            width: 100%;
            display: flex;

            .RoutineElement_subject_name {
                flex: 1;
                display: flex;
                justify-content: flex-start;
            }

            .RoutineElement_time {
                flex: 1;
                display: flex;
                justify-content: flex-start;
            }

            .RoutineElement_day {
                flex: 1;
                display: flex;
                justify-content: flex-start;
            }
        }
    }
}
