.ModeratorFormContainer {
    padding: 1.5rem;
    border-radius: 30px;
    width: 40%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #ffffff;
    border: #282b2e 1px solid;

    .action_row {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
}

.ModeratorFormInputsContainer {
    // background-color: #282b2e;
    background-color: #ffffff;
    // border: #282b2e 1px solid;
    padding: 10px;
    border-radius: 5px;

    .form_row {
        display: flex;
        flex-direction: row;
        justify-content: center;
        font-size: 1.4rem;
    }

    .form_field {
        flex: 1;
        display: flex;
        flex-direction: column;
        margin: 5px;
        align-items: flex-start;
    }

    .error {
        color: red;
        font-size: 1rem;
        margin-top: 5px;
    }

    .ModeratorFormBtns {
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin: 20px;
    }
}

.ModeratorFormInput {
    background-color: #303245;
    border-radius: 12px;
    border: 0;
    box-sizing: border-box;
    color: #eee;
    font-size: 18px;
    // height: 50px;
    outline: 0;
    padding: 10px;
    width: 100%;

    transition: all 0.3s ease-in-out;
    &:focus {
        border: 1px solid #5abb7a;
    }
    margin: 10px 0;
}
