.FormTitleDescription_container {
	width: 80%;
	display: flex;
	flex-direction: column;
	border-radius: 10px;
	margin: 10px 0;
	padding: 10px;

	// border: 2px solid #e0e0e0;
	box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

	.form_title_description_title {
		margin-bottom: 5px;

		input {
			width: 100%;
			padding: 5px 0;
			font-size: 1.8rem;
			font-weight: 600;
			outline: none;
			border: none;
			border-bottom: 1px dotted #e0e0e0;
		}
	}

	.form_title_description_description {
		margin: 15px 0;

		input,
		textarea {
			width: 100%;
			padding: 5px 0;
			font-size: 1rem;
			font-weight: 500;
			outline: none;
			border: none;
			border-bottom: 1px dotted #e0e0e0;
		}
	}

	.class_info_container {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-gap: 10px;
		margin-bottom: 10px;
		font-size: 1rem;
		// background-color: aqua;

		.class_info_item {
			display: flex;
			flex-direction: row;
			align-items: center;
			padding: 5px;
			text-align: start;
			// background-color: aquamarine;

			span {
				margin-right: 5px;
			}

			input {
				outline: none;
				border: none;
				border-bottom: 1px dotted #e0e0e0;
				padding: 5px;
				font-size: 1rem;
			}
		}
	}

	@media screen and (max-width: 1200px) {
		.class_info_container {
			display: grid;
			grid-template-columns: 1fr;
			// grid-template-rows: 1fr 1fr;
			grid-gap: 10px;
			margin-bottom: 10px;
			font-size: 1rem;
			// background-color: aqua;

			.class_info_item {
				display: flex;
				flex-direction: row;
				align-items: center;
				padding: 5px;
				text-align: start;
				// background-color: aquam
			}
		}
	}

	.action_container {
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		align-items: center;
		padding: 10px;
	}
}
.btn {
	font-size: 1rem;
	margin: 0 10px;
	color: #000;
	padding: 8px;
	border: 1px solid #e0e0e0;
	border-radius: 5px;
	cursor: pointer;

	&:hover {
		background-color: #e0e0e0;
	}
}
