.classpage_container {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .classpage_title {
        width: 100%;

        .classpage_title_text {
            font-size: 1.5rem;
            margin-top: 2rem;
            font-weight: bold;
            color: #414c5a;
            text-align: left;
        }
    }

    .mid_section {
        width: 80vw;
        flex: 0.4;
        display: flex;
        flex-direction: column;
        // background-color: #e01818;

        .mid_section_left {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            // background-color: aqua;

            select {
                display: flex;
                flex-direction: row;
                position: relative;
                width: 220px;
                height: 40px;
                padding: 5px;
                border-radius: 5px;
                font-size: 1rem;
                outline: none;
                background: #f9d671;

                &:hover {
                    box-shadow: 0px 0px 10px #666;
                }
            }

            option {
                padding: 0 30px 0 10px;
                min-height: 40px;
                display: flex;
                align-items: center;
                background: #f9d671;
                // border-top: #222 solid 1px;
                position: absolute;
                top: 0;
                width: 100%;
                pointer-events: none;
                order: 2;
                z-index: 1;

                box-sizing: border-box;
                overflow: hidden;
                white-space: nowrap;
            }
            .option:hover {
                background: #666;
            }

            .create_course {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                width: 220px;
                height: 40px;
                padding: 5px;
                border-radius: 5px;
                background: #f9d671;
                cursor: pointer;

                &:hover {
                    box-shadow: 0px 0px 10px #666;
                }
            }
        }
    }

    .bottom_section {
        margin-top: 1rem;
        display: flex;
        align-items: center;
        flex-direction: column;
        padding-top: 20px;

        border: 1px solid #d2e5f6;

        .classes_container {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
            // grid-column-gap: 30px;
            // grid-row-gap: 20px;
            width: 70vw;
            // border: 1px solid #d2e5f6;

            // background-color: #364fc0;

            .class_tile {
                height: 280px;
                display: flex;

                flex-direction: column;
                align-items: center;
                justify-content: center;

                cursor: pointer;
                // background-color: rgba(249, 214, 113, 0.25);

                .class_tile_image {
                    // width: 280px;
                    height: 250px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    border-radius: 5px;
                    // background-color: #36c054;
                    box-shadow: 0px 0px 10px #666;
                    overflow: hidden;

                    &:hover {
                        box-shadow: rgba(152, 59, 233, 0.2) 0px 7px 29px 0px;
                        .class_tile_title {
                            background-color: #983be9;
                            color: white;
                        }
                    }

                    .class_image {
                        width: 200px;
                        height: 150px;
                        padding: 20px;
                        // background-color: #ffffff;
                    }
                }

                .class_tile_title {
                    font-size: 1.5rem;
                    font-weight: bold;
                    width: 100%;
                    background-color: #f9d671;
                    flex: 1;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    transition: all 0.3s ease-in-out;
                }
            }

            .no_courses {
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;

                .no_courses_text {
                    color: #1a5cce;
                    font-size: 1.5rem;
                    font-weight: bold;
                }
            }
        }

