.popup_container {
    padding: 1rem;
    border-radius: 10px;
    width: 45%;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;

    .action_row {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    select {
        width: 220px;
        padding: 0.5rem;
        border-radius: 10px;
        font-size: 20px;
        color: #414c5a;
        font-weight: bold;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        outline: none;
        background-color: #f9d671;
    }

    .form {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 100%;
    }

    .form_row {
        display: flex;
        flex: 1;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        margin-top: 1rem;
        gap: 10px;
    }

    .popup_form_field {
        display: flex;
        flex-direction: column;
        width: 100%;
        flex: 1;
        margin-bottom: 1rem;

        label {
            font-size: 14px;
            color: #414c5a;
            font-weight: bold;
            margin-bottom: 0.5rem;
            align-self: flex-start;
        }

        input,
        textarea {
            width: 100%;
            height: 30px;
            padding: 0.5rem 0rem;
            padding-left: 0.2rem;
            border-radius: 5px;
            font-size: 16px;
            color: #414c5a;
            font-weight: bold;
            align-items: center;
            justify-content: center;
            outline: none;
            border: none;
            background-color: #f9d671;
        }
    }
    .popup_error {
        color: red;
        font-size: 14px;
        margin-top: 0.5rem;
    }

    .popup_choice_col {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .popup_choice_row {
            display: flex;
            flex-direction: row;
            justify-content: space-around;

            .popup_choice {
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                width: 120px;
                padding: 1rem;
                border-radius: 5px;
                font-size: 1.2rem;
                font-weight: bold;
                cursor: pointer;

                .popup_check {
                    height: 20px;
                    width: 20px;
                }

                .popup_label {
                    margin-left: 0.5rem;
                    font-size: 20px;
                }
            }
        }

        .popup_error {
            color: red;
            font-size: 1rem;
            margin-top: 5px;
        }
    }

    .popup_btn_row {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        gap: 20px;
        margin: 1rem;

        .popup_btn {
            width: 120px;
            padding: 1rem;
            border-radius: 5px;
            font-size: 1.2rem;
            font-weight: bold;
            cursor: pointer;
            color: white;
            background-color: #5abb7a;

            @media screen and (max-width: 768px) {
                padding: 0.5rem;
                border-radius: 10px;
                margin: 4px;
            }
        }
    }

    // @media screen and (max-width: 768px) {
    //     .input_container {
    //         width: 80%;
    //     }
    // }
}
