.fadeOut {
	opacity: 0;
	visibility: hidden;
	width: 0;
	height: 0;
	transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
}

.fadeIn {
	opacity: 1;
	visibility: visible;
	height: 250px;
	transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
}

.quizRow {
	display: flex;
	flex-direction: column;
	padding: 10px 5px;
	border-bottom: 1px solid #e6e6e6;
	border-radius: 10px;
	font-size: 18px;
	color: #515151;

	&:hover {
		background-color: #f5f5f5;
	}

	.quizRowTop {
		height: 50px;
		display: flex;
		gap: 20px;
		align-items: center;
		padding: 0px 5px;
		cursor: pointer;

		.quizRowTopIcon {
			padding: 5px;
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			color: white;
			background-color: #00a0e9;
		}

		.quizRowTopTitle {
			flex: 1;
			text-align: start;
			font-size: 20px;
		}

		.quizRowTopDue {
			flex: 0.4;
		}
	}

	.quizRowBody {
		width: 100%;

		.quizRowBodyContainer {
			margin-top: 10px;
			border-top: #515151 1px solid;
			display: flex;
			align-items: flex-start;
			flex-direction: column;
			.quizCreatedDate {
				padding: 15px 0px;
			}

			.quizDesc {
				font-size: 18px;
				padding: 15px 0px;
				// height: 110px;
			}

			.quizCreatedDate {
				font-size: 14px;
			}

			.quizPageLink {
				height: 50px;
				background-color: #983be9;
				width: 100%;
				display: flex;
				gap: 20px;
				justify-content: center;
				align-items: center;
				border-radius: 10px;
				color: white;
				cursor: pointer;
			}
		}
	}
}
