.developmentContainer {
    // width: 100%;
    height: 100vh;
    // background-color: burlywood;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    .header_container_dev {
        width: 100vw;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: 0px 0px 10px #00000029;
    }
    //components
    .progress_indicator {
        height: 20px;
        width: 40px;
        margin-right: 20px;
        border-radius: 10%;
    }

    .developmentContainerBody {
        width: 100vw;
        flex: 1;
        // background-color: aqua;

        display: flex;

        @media (max-width: 768px) {
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }

        .leftContainer {
            width: 25%;
            min-width: 280px;

            // background-color: rgb(219, 219, 219);

            .heading_text {
                color: #46178f;
            }
            .subheading_text {
                padding: 5px 25px;
                text-align: start;
                text-align: justify;
            }

            ul {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                padding: 5% 20%;
                // background-color: aqua;

                li {
                    // width: 250px;
                    margin-bottom: 8px;
                    text-align: start;
                }
            }
        }

        .midContainer {
            width: 50%;
            min-width: 280px;
            margin: 0px 10px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .postHolderContainer {
                height: 75vh;
                overflow-y: scroll;
                -ms-overflow-style: none;
                scrollbar-width: none;
                // background-color: #46178f;

                .reportList {
                    width: 100%;
                    // background-color: bisque;

                    .reportListItem {
                        display: flex;
                        flex-direction: row;
                        justify-content: flex-start;
                        align-items: center;
                        margin: 10px;
                        border-radius: 5px;
                        min-width: 250px;
                        // background-color: aqua;

                        .voteInfo {
                            display: flex;
                            // width: 60px;
                            flex-direction: column;
                            justify-content: flex-start;
                            align-items: center;
                            margin-right: 5px;
                            padding: 0 10px;
                            // background-color: bisque;

                            .voteIcon {
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                width: 30px;
                                height: 40px;
                                font-size: 30px;
                                cursor: pointer;
                                color: #5f5f69;
                                border-radius: 5px;
                                background-color: bisque;
                                //background-color: rgb(243, 10, 10);

                                &:hover {
                                    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px
                                            0px,
                                        rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
                                }
                            }
                            .voting_count {
                                color: #46178f;
                            }
                        }

                        .reportInfo {
                            display: flex;
                            flex-direction: column;
                            justify-content: flex-start;
                            align-items: center;
                            text-align: start;
                            padding-left: 10px;
                            padding: 10px;
                            width: 100%;
                            border-radius: 5px;
                            background-color: #f6f6f6;
                            border: #ffffff solid 5px;
                            box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px,
                                rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;

                            .reportHeader {
                                width: 100%;
                                display: flex;
                                justify-content: flex-start;
                                align-items: center;
                                font-weight: 700;
                                color: #46178f;
                                // background-color: #46178f;
                            }

                            .reportBody {
                                width: 100%;
                                // height: 100%;
                                margin: 20px 0px;
                                display: flex;
                                justify-content: flex-start;
                                align-items: center;
                                color: #46178f;

                                // background-color: chartreuse;
                            }

                            .reportTime {
                                width: 100%;
                                // height: 100%;
                                display: flex;
                                justify-content: flex-start;
                                align-items: center;
                                font-size: 14px;
                                color: #46178f;
                                // background-color: aquamarine;
                            }
                        }
                    }
                }
            }
            .postHolderContainer::-webkit-scrollbar {
                display: none;
            }
            .pagination {
                padding: 10px;
            }
        }

        .rightContainer {
            width: 25%;
            min-width: 280px;

            // background-color: rgb(226, 226, 226);

            .Icon_desc_container {
                // background-color: aqua;
                margin: 0px 10px;
                .Icon_element {
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    margin: 10px;
                    font-size: 15px;

                    .Icon_circle {
                        height: 20px;
                        width: 40px;
                        margin-right: 20px;
                        border-radius: 10%;
                    }
                }
            }

            .Button_desc_container {
                .Button_element {
                    background-color: #ffffff;
                    color: rgb(0, 0, 0);
                    padding: 20px;
                    font-size: 20px;
                    margin: 20px;
                    border-radius: 10px;
                    cursor: pointer;
                    border: 1px solid #46178f;

                    &:hover {
                        transition: 0.3s;
                        background-color: #46178f;
                        color: rgb(255, 255, 255);
                        padding: 20px;
                        font-size: 20px;
                        margin: 20px;
                        border-radius: 10px;
                        border: 1px solid #46178f;
                        cursor: pointer;
                    }
                }
            }
        }
    }
}
