.ModClassListItemContainer {
	width: 70vw;
	min-width: 300px;
	height: 50px;
	background-color: #282b2e;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
	padding: 10px;
	margin: 5px;
	text-align: start;

	.ModClassListItemName {
		flex: 1.5;
		display: flex;
		flex-direction: row;
		justify-content: center;
	}

	.ModClassListItemActions {
		flex: 1.5;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
	}

	@media only screen and (max-width: 800px) {
		.ModClassListItemContainer {
			padding: 0px;
			margin: 0px;
			font-size: 1rem;
		}
		.ModClassListItemActions {
			flex: 1.5;
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
		}
	}
}
