.ModeratorPageContainer {
	.ModeratorPageHeader {
		.ModeratorPageHeaderText {
			font-size: 1.5rem;
			font-weight: bold;
			color: #fff;
			text-align: center;
			padding: 10px;
		}




		.ModeratorPageHeaderButtonArea {
			padding-top: 10px;
			display: flex;
			justify-content: center;
			align-items: center;
		}



	@media only screen and (max-width: 850px) {
	
		.ModeratorPageHeaderText {
			font-size: 1.2rem;
			padding: 5px;
	
		}
	
		.ModeratorPageHeaderButtonArea {
			padding-top: 5px;
			font-size: .8rem;
		}


	}









	}

	.ModeratorPageBody {
		.ModeratorPageBodyTable {
			height: 100vh;
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: center;
			padding: 10px;
			border-bottom: 5px solid #222528;

			&:hover {
				border-bottom: 5px solid #282b2e;
			}

			.ModeratorPageBodyTableRow {
				overflow: scroll;
			}
			.ModeratorPageBodyTableRow::-webkit-scrollbar {
				display: none;
			}
		}
	}
	@media only screen and (max-width: 850px){

		.ModeratorPageBody {
			height: 100vh;
		}

		.ModeratorPageBodyTable {
			height: 100vh;
		}

		.ModeratorPageBodyTableRow {
			overflow: scroll;
		}


	}
	
}



