.popup-box {
    position: fixed;
    background: #00000050;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
}
.white_thin_line {
    flex: 1;
    height: 1px;
    background-color: #282b2e;
    margin: 0px 10px;
}
.white_thin_icons {
    font-size: 18px;
    height: 30px;
    width: 30px;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: #282b2e;
    cursor: pointer;
}

.box::-webkit-scrollbar {
    display: none;
}

.box {
    position: relative;
    width: 85%;
    margin: 0 auto;
    height: auto;
    height: 80vh;
    margin-top: calc(100vh - 90vh - 20px);
    background: #fff;
    border-radius: 10px;
    padding: 5px;
    border: 1px solid #282b2e;
    overflow: auto;
    color: rgb(0, 0, 0);
    scrollbar-width: none;
    background-color: #fff;
    -ms-overflow-style: none; /* Internet Explorer 10+ */
    scrollbar-width: none; /* Firefox */

    .close-icon {
        content: "x";
        cursor: pointer;
        position: fixed;
        right: calc(7.5% - 20px);
        top: calc(100vh - 90vh - 33px);
        background: 282b2e;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        line-height: 20px;
        text-align: center;
        border: 1px solid #999;
        color: #fff;
        font-size: 20px;
        background-color: black;
    }

    .popup-box-materials-container {
        border-radius: 10px;

        color: #282b2e;
        // height: 100%;
        padding: 10px;
        display: flex;
        flex-direction: column;

        .topic_header {
            .topic_header_Image {
                height: 300px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                // align-items: flex-start;
                border: 2px solid #282b2e;
                border-radius: 10px;

                @media (max-width: 768px) {
                    height: 250px;
                }

                //media

                .topic_header_course_code {
                    opacity: 0;
                    filter: alpha(opacity=0);
                    transition: 0.5s;
                    align-self: flex-start;
                    background-color: rgba(75, 75, 75, 0.9);
                    padding: 5px 10px;
                    margin: 3px;
                    border-radius: 5px;
                }

                &:hover {
                    .topic_header_course_code {
                        opacity: 1;
                        filter: alpha(opacity=100);
                        transition: 0.5s;
                    }
                }
                .topic_header_Icon {
                    width: 100%;
                    background-color: rgba(75, 75, 75, 0.8);
                    overflow: hidden;
                    display: flex;
                    align-items: center;
                    color: black;
                    border-radius: 10px;
                    //image fit

                    img {
                        height: 80px;
                        width: 80px;
                        border-radius: 50%;
                        object-fit: cover;
                        border: 2px solid #fff;
                        padding: 2px;
                        align-self: flex-start;
                        object-fit: contain;
                    }

                    .topic_header_info_left {
                        display: flex;
                        flex: 1;
                        flex-direction: column;
                        justify-content: center;
                        align-items: flex-start;
                        padding-left: 20px;
                        color: white;

                        .topic_header_className {
                            font-size: 20px;
                            font-weight: bold;
                            margin-bottom: 2px;
                        }
                        .topic_header_subject_name {
                            font-size: 15px;
                            font-weight: 500;
                            margin-bottom: 2px;
                        }

                        .topic_header_chapter_info {
                            font-size: 14px;
                        }
                    }
                    .topic_header_info_right {
                        flex: 0.2;
                        display: flex;
                        color: #282b2e;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        padding-right: 20px;
                        font-size: 15px;
                    }

                    @media (max-width: 768px) {
                        // flex-direction: column;

                        .topic_header_info_left {
                            padding-left: 0;
                            padding-right: 0;
                        }

                        img {
                            align-self: center;
                        }

                        .topic_header_info_left {
                            align-items: center;
                        }
                        .topic_header_info_right {
                            display: none;
                        }
                    }
                }
            }
        }
    }
    .topic_header_chapter_Materials {
        padding: 0px 10px;
        display: flex;
        flex-direction: column;
        color: #282b2e;
        .topic_header_chapter_Materials_header {
            // background-color: black;
            border: 1px solid #282b2e;
            padding: 10px 5px;
            border-radius: 10px;
            margin: 5px 0;

            .topic_header_chapter_Materials_header_title {
                font-size: 18px;
                font-weight: bold;
                padding-bottom: 10px;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .title {
                    flex: 1;
                    font-size: 18px;
                    font-weight: bold;
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                }

                .white_thin_line {
                    flex: 1;
                    height: 1px;
                    background-color: #282b2e;
                    margin: 0px 10px;
                }
                .white_thin_icons {
                    font-size: 18px;
                    height: 30px;
                    width: 30px;
                    font-weight: bold;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    color: white;
                    background-color: #282b2e;
                    cursor: pointer;
                }
            }

            .topic_header_chapter_Materials_header_item {
                // border: 5px solid rgba(75, 75, 75, 0.8);
                padding: 5px;
                border-radius: 10px;
                margin: 5px 0px;
                font-size: 15px;
                background-color: rgba(75, 75, 75, 0.8);
                color: white;

                &:hover {
                    background-color: rgba(75, 75, 75, 1);
                }
                .topic_header_chapter_Materials_component {
                    display: flex;
                    flex-direction: row;
                }
                .materials_container {
                    flex: 1;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    padding: 5px;
                    border-radius: 10px;
                }
                .materials_download_container {
                    flex: 0.2;
                    display: flex;
                    justify-content: center;
                    flex-direction: column;
                    align-items: center;
                    // border: 1px solid rgb(88, 88, 88);

                    .download_icon {
                        font-size: 40px;
                        cursor: pointer;
                    }

                    .progressBar {
                        width: 100px;
                    }
                }
            }
            .upload_book_btn {
                font-size: 15px;
                font-weight: bold;
                color: rgb(0, 0, 0);
                background-color: #ededed;
                border-radius: 10px;
                padding: 5px;
                cursor: pointer;
            }

            .BookInfocard {
                display: flex;
                flex-direction: row;
                gap: 10px;
                justify-content: center;
                align-items: center;
                padding: 5px;
                border-radius: 10px;
                background-color: rgba(75, 75, 75, 0.8);
                color: white;
                margin: 5px 0px;
                font-size: 15px;
                &:hover {
                    background-color: rgba(75, 75, 75, 1);
                }
                .bookInfoRowMid {
                    flex: 1;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                }

                .bookDOwnloadIcon {
                    font-size: 40px;
                    
                }
            }
        }
    }
}
