.ModeratorDevelopmentContainer {
	height: 100vh;
	padding: 10px;
	color: black;
	display: flex;
	flex-direction: row;
	background-color: #282b2e;

	.left {
		flex: 1.5;
		// text-align: start;
		// padding: 20px;
		// min-width: 200px;
		// background-color: #5e1c1c;

		.left_top {
			color: azure;
			display: flex;
			justify-content: space-between;
            justify-content: center;
            align-items: center;
            // padding: 10px;
			// background-color: aqua;

			.arrows {
				display: flex;
				justify-content: space-between;

				.Arrow {
					color: azure;
					font-size: 30px;
					cursor: pointer;
				}
			}
		}

		.postContainer {
			overflow-y: scroll;
			height: 100%;
			-ms-overflow-style: none;
			scrollbar-width: none;

			.reportListItem {
				background-color: azure;
				padding: 10px;
				border-bottom: 1px solid black;
				margin: 10px 0px;
				border-radius: 5px;
				cursor: pointer;

				//hover
				&:hover {
					background-color: antiquewhite;
				}

				div {
					padding: 2px 0px;
				}
				.textContainer {
					white-space: nowrap;
					min-width: 180px;
					overflow: hidden;
                    text-align: left;
					text-overflow: ellipsis;
					min-width: 180px;
				}
			}
		}
		.postContainer::-webkit-scrollbar {
			display: none;
		}
	}

	.mid {
		flex: 3;
		background-color: #282b2e;

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		.label_edit_post {
			color: azure;
			font-size: 20px;
			font-weight: bold;
			margin-bottom: 10px;
		}

		.post_card {
			width: 500px;
			background-color: azure;
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			padding: 10px;
			border-radius: 5px;
			box-shadow: 0px 0px 5px rgb(255, 255, 255);
			resize: both;
			overflow: auto;

			input {
				// background-color: azure;
				border: none;
				border-radius: 5px;
				padding: 5px;
				font-size: 20px;
				color: azure;
				background-color: #282b2e;
				margin: 10px 0;
			}
			textarea {
				// background-color: azure;
				border: none;
				border-radius: 5px;
				padding: 5px;
				width: 100%;
				font-size: 20px;
				color: azure;
				resize: vertical;
				box-sizing: border-box;
				margin: 10px 0;
				background-color: #282b2e;
				min-height: 40px;
			}

			.status_container {
				display: flex;
				flex-direction: row;
				margin: 10px 0;
				justify-content: space-between;
				align-items: center;
				width: 100%;

				.currentStatus {
					display: flex;
					flex-direction: row;
					justify-content: flex-start;
					align-items: center;

					.progress_indicator {
						margin-left: 10px;
					}
				}

				.changeStatusBtn {
					color: rgb(9, 13, 13);
					font-weight: bold;
					cursor: pointer;
					padding: 5px;
					// background-color: black;
					border: 1px solid rgb(9, 13, 13);
					border-radius: 5px;
				}
			}

			.options_container {
				margin: 10px 0px;
				display: flex;
				justify-content: space-around;
				width: 100%;

				// background-color: #282b2e;

				.optionBtn {
					padding: 10px;
					margin: 0px 5px;
					// background-color: #ef7b6b;
					border-radius: 5px;
					cursor: pointer;

					&:hover {
						transition: all 0.3s ease-in-out;
						scale: 1.1;
					}
				}
			}

			.post_footer {
				text-align: start;
			}
		}
		.btn_holder {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			background-color: azure;
			padding: 5px;
			border-radius: 5px;

			.btn_edit {
				background-color: #282b2e;
				color: azure;
				border: none;
				border-radius: 5px;
				padding: 10px;
				font-size: 20px;

				cursor: pointer;
			}
			.btn_delete {
				background-color: #282b2e;
				color: azure;
				border: none;
				border-radius: 5px;
				padding: 10px;
				font-size: 20px;

				cursor: pointer;
			}
		}
	}
	.right {
		flex: 1;
		background-color: aqua;
	}
}
