.ModSubjectListItemContainer {
	width: 70vw;
	height: 50px;
	background-color: #282b2e;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
	padding: 10px;
	margin: 5px;
	text-align: start;

	.ModSubjectListItemIcon {
		flex: 1.5;
		width: 30px;
		height: 30px;
		margin-right: 10px;
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #fff;

		img {
			height: 30px;
		}
	}

	.ModSubjectListItemName {
		flex: 1.5;
		display: flex;
		flex-direction: row;
		justify-content: center;
	}
	.ModSubjectListItemActions {
		flex: 1.5;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
	}
}
