.form_header_container {
	height: 65px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	background-color: #fefffe;

	.form_header_logo {
		display: flex;
		flex: 1;
		padding-left: 15px;
		align-items: center;
		cursor: pointer;

		img {
			width: 50px;
			height: 50px;
		}

		span {
			font-size: 1.2rem;
			font-weight: 600;
			color: #5e6268;
			margin-left: 0.7rem;
		}
	}

	.form_header_right {
		display: flex;
		flex: 1;
		flex-direction: row;
		justify-content: flex-end;
		align-items: center;
		padding: 5px;
		// background-color: aquamarine;

		.form_header_icon {
			width: 30px;
			height: 30px;
			display: flex;
			align-items: center;
			justify-content: center;
			margin: 0 10px;
			padding: 8px;
			font-size: 1.2rem;
			color: grey;
			cursor: pointer;
			border-radius: 50%;
			transition: all 0.2s ease-in-out;
			// background-color: antiquewhite;

			&:hover {
				background-color: thistle;
			}

			img {
				width: 40px;
				height: 40px;
				border-radius: 50%;
			}
		}
	}
}
