$backgroundColor: white;
$blueBtn: #46178f;
$blueBtnHover: #983be9;
$whiteBtn: #aaaaaa;
$fontLiteGray: #544837;
$deepBlack: #070c19;
$liteRed: #ff736b;

nav {
    background: $blueBtnHover;
    width: min-content;
    display: block;
    padding: 1.7rem 0.7rem;
    z-index: 2;
    position: fixed;
    // left: 50%;
    // transform: translateX(-50%);
    // bottom: 2rem;
    left: 15px;
    bottom: 50%;
    transform: translateY(50%);
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    display: flex;
    color: $whiteBtn;
    font-size: 1.1rem;
}

nav div {
    background: transparent;
    padding: 0.9rem;
    display: flex;
    color: $whiteBtn;
    font-size: 1.1rem;
    cursor: pointer;
}
nav div:hover {
    background: rgba(0, 0, 0, 0.3);
    border-radius: 25px;
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
    border-radius: 25px;
}

nav div.active {
    background: $blueBtn;
    color: white;
    border-radius: 25px;
}

nav a.active {
    background: $blueBtn;
    color: white;
    border-radius: 25px;
}

@media only screen and (max-width: 700px) {
    nav {
        background: $blueBtnHover;
        width: max-content;
        display: block;
        padding: 1rem 2rem;
        z-index: 2;
        position: fixed;
        left: 50%;
        transform: translateX(-50%);
        bottom: 2rem;
        display: flex;
        flex-direction: row;
        gap: 0.8rem;
        border-radius: 3rem;
        backdrop-filter: blur(15px);
    }
    nav a {
        padding: 0.5rem;
    }
    nav div {
        padding: 0.5rem;
    }
}
