.ModeratorCourseListHeader_Container {
	width: 70vw;
	min-width: 300px;
	height: 50px;
	background-color: #282b2e;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
	padding: 10px;
	margin: 5px;
	text-align: start;

	.ModeratorCourseListHeader_Container_Name {
		flex: 1.5;
		text-align: center;

	}

}


@media only screen and (max-width: 800px) {

	.ModeratorCourseListHeader_Container {

		padding: 0px;
		margin: 0px;
		font-size: 1rem;


	}
	.ModeratorCourseListHeader_Container_Name{
			flex: 1.5;
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			padding-left: 5px;
	}
}