.tables_container {
    padding: 10px 0px;
    margin: 10px 0px;
    box-shadow: rgba(115, 130, 146, 0.3) 0px 0px 0px 3px;
    border-radius: 10px;

    &:hover {
        box-shadow: rgba(115, 130, 146, 0.5) 0px 0px 0px 3px;
    }

    .vertical_bar {
        width: 5px;
        height: 100%;
        background-color: rgba(255, 0, 0, 0.5);
        margin-left: 10px;
    }

    .ShowSubjectTableContainer {
        box-shadow: rgba(43, 255, 0, 0.3) 0px 0px 0px 3px;
    }

    .class_table {
        width: 100%;
        height: 60px;
        color: rgb(255, 255, 255);
        box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 0px;
        display: flex;
        align-items: center;
        // background-color: #ddd;
        cursor: pointer;

        .class_icon {
            height: 40px;
            padding: 10px;
        }

        .class_name {
            flex: 2;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: flex-start;
        }
        .class_cover {
            height: 50px;
            transition: all 0.2s ease-in-out;
            &:hover {
                transform: scale(4);
            }
        }

        .class_Operations {
            display: flex;
            justify-content: center;
            align-items: center;
            flex: 0.5;
            cursor: auto; //default value is
            border-left: #ddd solid 1px;

            .class_Operations_dropdown {
                padding: 10px;
                &:hover {
                    color: black;
                    background-color: rgb(255, 255, 255);
                    transition: all 0.2s ease-in-out;
                }
            }
        }
    }

    //dropdown
    .navbar {
        overflow: hidden;
    }

    .dropdown {
        float: left;
        overflow: hidden;
    }

    .dropdown .dropbtn {
        font-size: 14px;
        border: none;
        outline: none;
        color: white;
        // padding: 14px 16px;
        background-color: inherit;
        font-family: inherit;
        margin: 0;
    }

    .dropdown-content {
        // display: none;
        position: absolute;
        background-color: #ffffff;
        min-width: 100px;
        // box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
        z-index: 1;
    }

    .dropdown-content-option {
        float: none;
        color: black;
        padding: 10px 10px;
        text-decoration: none;
        display: block;
        text-align: left;
    }

    .dropdown-content-option:hover {
        background-color: #ddd;
    }

    .subject_table_container {
        overflow: auto;
        scrollbar-width: none;
    }

    .subject_table_selected {
        background-color: rgba(255, 255, 255, 0.3);
    }
    .subject_table {
        width: 100%;
        height: 60px;
        box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 0px;
        display: flex;
        // gap: 10px;
        justify-content: space-around;
        align-items: center;
        margin-bottom: 5px;

        &:hover {
            background-color: rgba(255, 255, 255, 0.3);
        }

        .SubjectRowMid {
            display: flex;
            flex: 2;
            // justify-content: space-between;
            align-items: center;
            // background-color: #ddd;
            gap: 50px;
            cursor: pointer;

            .subject_table_icon {
                padding: 10px;
                width: 80px;
                background-color: #ddd;
            }

            .subject_table_name {
                flex: 1;
                display: flex;
                justify-content: flex-start;
            }

            .subject_table_cover {
                flex: 0.2;
                display: flex;
                justify-content: center;
                align-items: center;

                img {
                    height: 30px;

                    &:hover {
                        transform: scale(2);
                    }
                }
            }
            .subject_table_courseCode {
                flex: 1;
            }
        }

        .subject_table_actions {
            display: flex;
            justify-content: center;
            align-items: center;
            flex: 1;
            border-left: 1px solid rgba(255, 0, 0, 0.5);

            .class_Operations_dropdown {
                padding: 10px;
                color: aqua;
                &:hover {
                    background-color: rgb(255, 255, 255);
                    transition: all 0.2s ease-in-out;
                }
            }
        }
    }
}

.topic_table {
    width: 100%;
    margin-bottom: 10px;
    scrollbar-width: none;
    padding: 10px 0px;
    .ShowChaptersTableContainer {
        cursor: pointer;
        display: flex;
        height: 60px;
        box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 0px;
        align-items: center;
        padding: 2px 0;

        .rowInfoHolder {
            display: flex;
            flex: 2;
            justify-content: center;
            align-items: center;
        }

        &:hover {
            background-color: rgba(255, 255, 255, 0.3);
        }

        .vertical_bar_chapters {
            width: 5px;
            height: 100%;
            background-color: rgba(0, 255, 0, 0.5);
            margin-left: 20px;
        }

        .chapterIconLog {
            // flex: 0.5;
            justify-content: center;
            align-items: center;
            width: 100px;
            background-color: #ddd;

            img {
                height: 40px;
            }
        }

        .ChapterSerial {
            padding: 20px;
        }

        .ChapterName {
            flex: 1.5;
            display: flex;
            justify-content: flex-start;
        }
        .Chapter_Cover_image_container {
            flex: 0.6;
            justify-content: center;
            align-items: center;
            background-color: #ddd;

            .Chapter_Cover_image {
                flex: 0.5;
                height: 50px;
                transition: all 0.2s ease-in-out;

                &:hover {
                    transform: scale(4);
                }
            }
        }

        .Chapter_Actions {
            border-left: rgba(0, 255, 0, 0.5) 1px solid;
            flex: 0.5;
            display: flex;
            gap: 20px;
            justify-content: center;
            align-items: center;
        }
    }
}
