.RoutineMakerContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 5px;
    .selectedDay {
        background-color: #e05353;
    }
    .routineRows {
        display: flex;
        width: 100%;
        height: 50px;

        .routineColsIndex {
            font-size: 15px;
            position: relative;
            display: flex;
            flex: 1;
            align-items: center;
            justify-content: center;
            border: 1px solid #00bcd4;

            .routineColsIndex_Delete {
                position: absolute;
                top: 0;
                left: 0;
                height: 20px;
                width: 20px;
                color: #ffffff;
                background-color: #e05353;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                opacity: 0.5;
                transition: opacity 0.3s ease-in-out;

                &:hover {
                    opacity: 1;
                }

                &:active {
                    opacity: 0.5;
                }
            }
        }

        .routineCols {
            flex: 1;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            border: 1px solid #00bcd4;
            font-size: 15px;

            //hover
            &:hover {
                background-color: #00bcd4;
                color: #ffffff;
            }

            input {
                width: 100%;
                height: 100%;
                border: none;
                background-color: transparent;
                color: #00bcd4;
                font-size: 1.2rem;
                text-align: center;

                &:hover {
                    background-color: #00bcd4;
                    color: #ffffff;
                }
            }
        }
    }
}
