.CoursesContainer {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
	grid-gap: 10px;
	padding: 10px;
	margin: 10px;
	justify-items: center;
	align-items: center;
}

@media only screen and (max-width: 800px) {
	.CoursesContainer {
		grid-template-columns: 1fr;
	}
}

@media only screen and (max-width: 400px) {
	.CoursesContainer {
		padding: 0px;
		margin: 0px;
	}
}
