.ModeratorPageContainer {
	.ModeratorPageHeader {
		.ModeratorPageHeaderFilterContainer {
			display: flex;
			flex-direction: row;
			justify-content: space-around;
			align-items: center;
			padding: 10px;
			margin-bottom: 5px;
			// background-color: #f6f8fd;

			.ModeratorPageHeaderFilterButton {
				border: none;
				background-color: #5abb7a;
				color: #ffffff;
				padding: 10px;
				font-size: 1.2em;
				outline: none;
			}
		}

		.course_data {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			padding: 10px;
			margin: 0px 10px;

			.course_info {
				display: flex;
				flex-direction: column;
				align-items: flex-start;

				.course_info_item {
					font-size: 1.2rem;
				}
			}

			.add_chapter {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				// background-color: #3316d8;
			}
		}
	}

	.ModeratorPageBodyTable {
		height: 80vh;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		padding: 10px;
		border-bottom: 5px solid #222528;

		&:hover {
			border-bottom: 5px solid #282b2e;
		}

		.ModeratorPageBodyTableRow {
			overflow-y: scroll;
		}
		.ModeratorPageBodyTableRow::-webkit-scrollbar {
			display: none;
		}
	}
}

