.QuestionCardContainer {
	width: 80%;
	border-radius: 10px;
	margin: 10px 0;
	padding: 10px;
	// border: 2px solid #e0e0e0;
	// background-color: #ffffff;
	box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

	.card_top {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;

		.question_row {
			width: 100%;
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 10px;
			// background-color: aqua;

			.question_container {
				width: 70%;
				display: flex;
				flex-direction: row;
				align-items: center;
				justify-content: flex-start;
				flex: 0.65;
				padding: 5px;
				border-radius: 5px;
				border: 1px solid #e0e0e0;
				// background-color: aqua;

				.question_index {
					font-size: 1.2em;
					padding: 0 10px;
					border-radius: 50%;
					-moz-border-radius: 50%;
					-webkit-border-radius: 50%;
					padding: 0;
					width: 30px;
					height: 30px;
					line-height: 30px;
					margin-right: 15px;
					box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px,
						rgba(0, 0, 0, 0.23) 0px 6px 6px;
				}

				.question_text_container {
					width: 100%;
					display: flex;
					flex-direction: column;
					align-items: flex-start;

					label {
						font-size: 11px;
					}

					.question_text {
						width: 100%;
						font-size: 1.1rem;
						font-weight: 500;
						outline: none;
						border: none;
						resize: none;
					}
				}
			}

			.question_file {
				width: 15%;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: flex-start;
				padding: 8px;
				border-radius: 5px;
				border: 1px solid #e0e0e0;
				// background-color: burlywood;

				label {
					font-size: 12px;
					margin-bottom: 10px;
				}

				input {
					width: 100%;
				}
			}

			.question_type {
				width: 15%;
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				flex: 0.3;
				padding: 10px;
				border-radius: 5px;
				border: 1px solid #e0e0e0;
				// background-color: cadetblue;

				label {
					font-size: 11px;
				}

				select {
					display: flex;
					flex-direction: row;
					justify-content: space-between;
					width: 100%;
					padding: 5px 0;
					font-size: 1rem;
					font-weight: 500;
					outline: none;
					border: none;
				}
			}
		}

		.preview_row {
			width: 100%;
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			align-items: center;
			padding: 0 10px;

			button {
				border-radius: 5px;
				padding: 10px;
				outline: none;
				border: none;
				box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
				cursor: pointer;
			}
		}
	}

	.card_mid {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		margin: 25px 0;

		.desc_row {
			width: 100%;
			display: flex;
			flex-direction: row;
			margin-bottom: 20px;

			.question_desc {
				width: 100%;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: flex-start;
				margin-right: 20px;

				label {
					font-size: 11px;
				}
			}

			.question_marks {
				width: 50%;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: flex-start;

				label {
					font-size: 11px;
				}
			}

			input,
			textarea {
				width: 100%;
				padding: 10px 0;
				font-size: 16px;
				outline: none;
				border: none;
				border-bottom: 1px dotted #e0e0e0;
				resize: vertical;
			}
		}

		.question_answer {
			width: 100%;

			.text_container {
				.text_input {
					display: flex;
					flex-direction: column;
					justify-content: center;
					align-items: flex-start;

					label {
						font-size: 11px;
					}
					textarea {
						width: 100%;
						padding: 10px 0;
						font-size: 16px;
						outline: none;
						border: none;
						border-bottom: 1px dotted #e0e0e0;
						resize: vertical;
						min-height: 25px;
					}
				}
			}

			.multiple_choice_container {
				display: flex;
				flex-direction: column;

				.multiple_choice_option {
					display: flex;
					flex: 1;
					flex-direction: row;
					justify-content: flex-start;
					align-items: center;
					margin-top: 20px;
					font-size: 1.1rem;
					// background-color: red;

					.radio {
						display: flex;
						flex: 0.2;
						padding: 10px 0;
						font-size: 1rem;
						outline: none;
						border: none;
						resize: none;
					}

					.text {
						padding: 10px 0;
						display: flex;
						flex: 1.5;
						font-size: 1rem;
						outline: none;
						border: none;
						border-bottom: 1px dotted #e0e0e0;
						resize: vertical;
						min-height: 25px;
					}
				}
			}

			.checkbox_container {
				display: flex;
				flex-direction: column;

				.checkbox_option {
					display: flex;
					flex: 1;
					flex-direction: row;
					justify-content: flex-start;
					align-items: center;
					margin-top: 20px;
					font-size: 1.1rem;

					.check {
						display: flex;
						flex: 0.3;
					}

					.text {
						padding: 10px 0;
						display: flex;
						flex: 1.5;
						font-size: 1rem;
						outline: none;
						border: none;
						border-bottom: 1px dotted #e0e0e0;
						resize: vertical;
						min-height: 25px;
					}
				}
			}

			.dropdown_container {
				width: 50%;
				margin-bottom: 5px;
				padding: 10px;
				border-radius: 5px;
				border: 1px solid #e0e0e0;

				select {
					display: flex;
					flex-direction: row;
					justify-content: space-between;
					width: 100%;
					padding: 5px 0;
					font-size: 1rem;
					font-weight: 500;
					outline: none;
					border: none;
				}
			}

			.file_upload_container {
				.upload_container {
					width: 60%;
					margin-bottom: 5px;
					padding: 10px;
					border-radius: 5px;
					border: 1px solid #e0e0e0;
					display: flex;
					flex-direction: column;
					justify-content: center;
					align-items: flex-start;
					cursor: pointer;

					label {
						font-size: 12px;
						margin-bottom: 10px;
					}
				}

				.preview_container {
					width: 100%;
					display: flex;
					flex-direction: row;
					justify-content: flex-start;
					align-items: center;
					padding: 0 10px;

					button {
						border-radius: 5px;
						padding: 10px;
						outline: none;
						border: none;
						box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
						cursor: pointer;
					}
				}
			}
		}
	}

	.card_bottom {
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		padding: 10px 0;

		.card_bottom_option {
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			font-size: 1rem;
			margin: 0 10px;
		}

		.card_bottom_clear_selection {
			font-size: 0.8rem;
			margin: 0 10px;
			color: #5f6269;
			font-weight: 600;
			border-radius: 5px;
			padding: 7px 5px;
			cursor: pointer;
			transition: all 0.3s ease-in-out;

			&:hover {
				background-color: #e5e5e5;
			}
		}
	}
}

.add_option_container {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	margin: 5px 0;
	font-size: 1.1rem;
}

.btn {
	font-size: 1rem;
	margin: 0 10px;
	color: #000;
	padding: 8px;
	border: 1px solid #e0e0e0;
	border-radius: 5px;
	cursor: pointer;
	box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

	&:hover {
		background-color: #e0e0e0;
	}
}

.delete {
	display: flex;
	flex: 0.2;
	align-items: center;
	justify-content: center;
	padding: 8px;
	border-radius: 5px;
	border: 1px solid #ffffff;

	&:hover {
		border: 1px solid #e0e0e0;
	}
}

.answer {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	flex: 0.3;
	padding: 8px;
	border-radius: 5px;
	border: 1px solid #ffffff;

	&:hover {
		border: 1px solid #e0e0e0;
	}

	label {
		font-size: 13px;
	}

	input {
		display: flex;
		flex: 0.2;
		padding: 10px 0;
		font-size: 1.1rem;
		outline: none;
		border: none;
		resize: none;
	}
}
