.mod_navbar_container {
	height: 100%;
	width: 350px;
	background-color: #282b2e;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	.mod_navbar_header {
		flex: 0.5;
		background-color: #ffffff;
		width: 100%;
		padding: 8px 0;
		display: flex;
		flex-direction: row;
		justify-content: space-evenly;
		align-items: center;
	}

	.mod_navbar_top {
		flex: 2;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		.mod_navbar_profile_container {
			.mod_navbar_profile_image {
				width: 120px;
				height: 120px;
				border-radius: 50%;
			}

			.mod_navbar_profile_name {
				font-size: 30px;
				color: #ffffff;
				font-weight: bold;
			}

			.mod_navbar_profile_role {
				font-size: 15px;
				color: #ffffff;
				font-weight: bold;
			}
		}
	}

	.mod_navbar_menu_btn {
		width: 200px;
		border-radius: 5px;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		font-size: 1.2rem;
		margin: 20px 0px;
		padding: 10px 20px;
		// background-color: aquamarine;

		&:hover {
			background-color: #ffffff;
			color: #282b2e;
			cursor: pointer;
		}
	}

	.logout_btn {
		border: 1px solid red;

		&:hover {
			color: white;
			background-color: red;
		}
	}

	.active {
		background-color: #5abb7a;
	}

	.menu_icon {
		margin-right: 10px;
	}

	.mod_navbar_middle {
		flex: 6;
		// background-color: #5abb7a;
	}

	.mod_navbar_bottom {
		flex: 2;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
}
