.ModeratorPageContainer {
	.ModeratorPageHeader {
		.ModeratorPageHeaderFilterContainer {
			display: flex;
			flex-direction: row;
			justify-content: space-around;
			align-items: center;
			padding: 10px;

			@media only screen and (max-width: 600px) {
			flex-direction: column;
	
			}
		
			background-color: #f6f8fd;
			margin: 0px 5px ;
		border-radius: 5px;

			.ModeratorPageHeaderFilterButton {
				border: none;
				background-color: #f6f8fd;
				color: #000000;
				padding: 10px;
				font-size: 1.2em;
				outline: none;
			}



		}
	}
	.ModeratorMaterialsPageBodyTop{
padding: 5px;
		height: 50px;
		display: flex;
		justify-content: center;
		align-items: center;



		.ModeratorMaterialsPageBodyTopBtn{
		flex: 1;
			background-color: #5abb7a;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 10px;
			border-radius: 5px;
			max-width: 200px;
			margin-right: 20px;
		

			//hover
			&:hover{
				cursor: pointer;
				background-color: #f6f8fd;
				color: #000000;
			}


		}
		.ModeratorMaterialsPageBodyTopHelpBtnContainer{
		flex: 1;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding-left: 20px;
		}












	}
.ChapterMaterialsTable{
background-color : #282B2E;
margin: 0px 5px ;
border-radius: 10px;
padding: 20px;


}

}
