.PopUpPromt_wrapper a {
    display: inline-block;
    text-decoration: none;
    padding: 10px;
    background-color: #5abb7a;
    margin: 5px;
    border-radius: 5px;
    color: #fff;
    font-size: 0.8rem;
    &:hover {
        background-color: #2d7444;
    }
}

.modal {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(77, 77, 77, 0.7);
    transition: all 0.4s;
    border-radius: 30px;
}

.modal:target {
    visibility: visible;
    opacity: 1;
}

.modal__content {
    padding: 20px;
    margin: 40px;
    border-radius: 4px;
    position: relative;
    background: rgb(0, 0, 0);
    padding: 1emrgb (0, 0, 0);
}

.modal__close {
    position: absolute;
    top: 10px;
    right: 10px;
    color: #585858;
    text-decoration: none;
}
