.modMaterialsItemsContainer {
	height: 60px;
	display: flex;
	align-items: center;
	padding: 5px 0px;
	margin: 5px 0px;
	box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px,
		rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
	// background-color: aqua;

	div {
		flex: 1;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.headerItem {
		font-weight: bold;
	}

	.BtnContainer {
		display: flex;
		flex-direction: row;

		.DeleteBtn {
			padding: 10px;
			border-radius: 5px;
			cursor: pointer;
			background-color: #5abb7a;
			color: white;
			margin-right: 4px;

			&:hover {
				background-color: #93c0a2;
			}
		}
	}
}
