$backgroundColor: white;
$blueBtn: #46178f;
$blueBtnHover: #983be9;
$whiteBtn: #aaaaaa;
$fontLiteGray: #544837;

.courseContainer {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	background-color: #46178f;
	padding: 5px;
	margin: 5px;
	height: 120px;
	border-radius: 20px;
	align-items: center;
	color: aliceblue;
	width: 300px;
	transition: all 0.2s ease-in-out;

	&:hover {
		background-color: $blueBtnHover;
		color: white;
	}
	cursor: pointer;
	img {
		width: 100px;
		height: 120px;
		object-fit: cover;
		border-radius: 20px;
	}
}

.courseContainerText {
	padding: 0px 5px;
	width: 200px;
	// background-color: #ffd700;
}

.courseContainerTextName {
	padding-bottom: 5px;
	font-size: 14px;
	font-weight: bold;
}

.courseContainerTextTitle {
	font-size: 12px;
}

.courseContainerTextPrice {
	font-weight: bold;
	color: #ffd700;
}

@media (min-width: 620px) and (max-width: 800px) {
	.courseContainer {
		width: 500px;
		justify-content: space-around;
		align-items: center;

		img {
			width: 150px;
		}
		.courseContainerTextName {
			padding-bottom: 10px;
			font-size: 20px;
			font-weight: bold;
		}
		.courseContainerTextTitle {
			font-size: 12px;
		}

		.courseContainerTextPrice {
			font-weight: bold;
			color: #ffd700;
			font-size: 20px;
		}
	}
}

@media only screen and (max-width: 400px) {
	.courseContainer {
		width: 300px;
		justify-content: space-around;
		align-items: center;

		img {
			width: 100px;
		}

		.courseContainerTextName {
			padding-bottom: 10px;
			font-size: 20px;
			font-weight: bold;
		}

		.courseContainerTextTitle {
			font-size: 12px;
		}

		.courseContainerTextPrice {
			font-weight: bold;
			color: #ffd700;
			font-size: 20px;
		}
	}
}
