.homepage_container {
    .homepage_title {
        width: 80vw;

        .homepage_title_text {
            font-size: 1.5rem;
            font-weight: bold;
            color: #414c5a;
            margin-top: 2rem;
            text-align: left;
        }
    }

    .homepage_info {
        width: 80vw;

        flex: 1;
        display: flex;
        flex-direction: row;
        justify-content: center;

        font-size: 1rem;
        color: #414c5a;
        margin-top: 1rem;
        text-align: left;

        //media
        @media (max-width: 768px) {
            flex-direction: column;
            align-items: center;
        }

        .teacher_details {
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;

            padding: 15px;
            border-radius: 5px;
            background-color: #ffffff;
            margin-right: 5px;
            box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;

            .teacher_details_top_row {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                // background-color: #36c064;

                .teacher_details_name {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    font-size: 2rem;
                    font-weight: bold;
                    color: #000000;
                    // background-color: #414c5a;
                }
                .teacher_details_xp {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-end;
                    margin-right: 3rem;
                    color: #a0a0a0;
                    font-size: 1rem;
                    // background-color: burlywood;

                    .teacher_details_xp_level {
                        font-size: 1.5rem;
                        color: #000000;
                    }

                    .teacher_details_xp_count {
                        font-size: 1.5rem;
                        color: #364fc0;
                    }
                }
            }
        }

        .notifications {
            width: 380px;
            padding: 15px;
            margin-left: 5px;
            border-radius: 5px;
            background-color: #ffffff;
            box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
            min-height: 360px;
            overflow: auto;

            @media (max-width: 768px) {
                margin-top: 50px;
                margin-left: 0px;
                flex-direction: column;
            }

            .notification_title {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                width: 100%;
                // background-color: aqua;

                .notification_title_left {
                    display: flex;
                    flex-direction: row;
                    font-size: 1.3rem;
                    color: #414c5a;
                    // background-color: aliceblue;
                    padding: 0.5rem;
                    // text-align: left;
                    align-items: center;
                    // justify-content: center;

                    .notification_icon {
                        align-items: center;
                        justify-content: center;
                        margin-right: 0.5rem;
                    }
                }
                .notification_title_right {
                    font-size: 1.2rem;
                    font-weight: bold;
                    color: #364fc0;
                    padding: 0.5rem;
                    // background-color: aliceblue;

                    .notification_title_right_text {
                        text-decoration: none;
                    }
                }
            }
            .reactCalendar {
                border: 0 !important;
            }
        }
    }
    .homepage_grid {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
        grid-gap: 1rem;
        margin-top: 50px;

        .title {
            display: flex;
            flex-direction: row;
            font-size: 1.3rem;
            justify-content: space-between;
            color: #414c5a;
            // background-color: aliceblue;
            padding: 0.5rem;
            // text-align: left;
            align-items: center;
            .add_button {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                font-size: 1.2rem;
                color: #414c5a;
                border: none;
                padding: 8px;
                border-radius: 5px;
            }
        }

        .fev_classes {
            flex: 1;
            box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
            border-radius: 5px;
            padding: 5px;
        }
        .notification_container {
            flex: 1;
            display: flex;
            flex-direction: column;

            .exam_notification_container {
                box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
                border-radius: 5px;
            }
        }
    }
}
